<template>
   <div id="content">
     <div class="top"></div>
           <el-tabs v-model="activeName" @tab-click="activeBtn">
                <el-tab-pane label="以文件方式" name="first">
                   <File v-if="activeName == 'first'"/>
                </el-tab-pane>
                <el-tab-pane label="数据源方式" name="second">
                    <Data v-if="activeName == 'second'"/>
                </el-tab-pane>
           </el-tabs>
   </div>
</template>

<script>
import FADE from "../animate/fade";
import File from './fileMode'
import Data from './dataMode'
import {
getPath
} from "../../api/api.js";
export default {
  mounted(){
   
  },
  data() {
    return {
        activeName: "first", //tab切换
        objMassage:{}
    };
  },
  methods: {
    //tab切换的方法
    activeBtn(tab, event) {
      console.log(tab, event);
    },
  },
  components: {
    FADE,
    File,
    Data
  }
};
</script>

<style scoped>

#content{
  width:100%;
  height:100%;
  background:url('../../assets/image/bg.jpg') no-repeat;
  background-size:cover
 
}
.top{
  width:100%;
  height:90px
}
</style>
<style>
 .el-tabs__nav-wrap::after{
  width:220px
}
.el-tabs__nav-wrap{
   
    left: 200px;
}
.el-tabs__header{
  width:80%
}
.el-tabs__item{
  color:white;
  font-weight: bold;
  padding: 0;
  text-align: center;
}
.el-tabs__active-bar{
  width:100px!important;
  
}
#tab-first{
  width:100px!important;
  margin-right:20px;
}
#tab-second{
  width:100px!important
}
/* .el-tabs{
   background:url('../../assets/image/bg.jpg') no-repeat;
   background-position: right
} */
</style>



