import axios from 'axios'
// axios.defaults.baseURL = 'http://192.168.38.10:81';
axios.defaults.withCredentials = true;  //配置允许跨域携带cookie
// axios.defaults.timeout = 1000 * 60;  //超时配置100000
axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';  //标识ajax请求
axios.defaults.headers.common['Content-Type'] = 'application/json;charset=utf-8';
//添加请求拦截器
axios.interceptors.request.use(config => {
    //在发送请求之前做些什么
    // if (window.sessionStorage.token) {
    //     config.headers.Authorization = window.sessionStorage.token;
    // } else {
    //     //route跳转到login
    //     if (config.url !== 'userlogin.do')
    //         window.location.href = '/login'
    // }
    return config
}, error => {
    // new Message({
    //     message: error,
    //     type: "error.data.error.message"
    // })
    return error.message;
})

//添加响应拦截器
axios.interceptors.response.use(response => {
    //对响应数据做处理
    // 在这里你可以判断后台返回数据携带的请求码
    if (response.status === 200) {
        switch (response.data.state) {
            case "0":
                return response.data;
                // break;
            case "1":
                if(response.data.msg === '需要登录'){
                    window.location.href = '/login'
                }
                return response.data;
                // break;
            case "10": {
                window.location.href = '/login';
                break;
            }
            default:
                return response.data
        }
    } else {
        return response.data
    }
}, error => {
 
    // //自定义的错误信息
    // Message({
    //     message: error,
    //     type: "error.data.error.message"
    // })
    return error.message;
})

export default axios


