import axios from './axiosSetting'
import {url} from "../../static/urlConfig"
// const url = 'http://192.168.65.150:8080'

//连接
export const connect =(params)=>{return axios.post(url+'/api/connect',params)}

//已连接列表
export const getClients =(params)=>{return axios.get(url+'/api/getClients')}

//断开
export const disconnect =(params)=>{return axios.post(url+'/api/disconnect',params)}

//开始

export const startSocketServer =(params)=>{return axios.post(url+'/api/startSocketServer',params)}

//停止

export const stopSocketServe =(params)=>{return axios.post(url+'/api/stopSocketServer',params)}

//在线日志
export const onlineLog =(params)=>{return axios.get(url+'/api/onlineLog')}

//存储路径及数据源
export const getPath =(params)=>{return axios.get(url+'/api/getPath')}


//离线传输开始

export const startOfflineServer =(params)=>{return axios.post(url+'/api/startOfflineServer',params)}

//离线传输停止

export const stopOfflineServer =(params)=>{return axios.post(url+'/api/stopOfflineServer',params)}

//离线日志
export const offlineLog =(params)=>{return axios.get(url+'/api/offlineLog')}


