// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import 'element-ui/lib/theme-chalk/index.css';
import ant from 'ant-design-vue'
import 'ant-design-vue/dist/antd.css';
import ElementUI from 'element-ui';
import App from './App'
import router from './router'


// import './js/flexible_css.debug.js';
// import './js/flexible.debug.js';
import echarts from 'echarts';
import Animate from "./assets/css/animate.css"
import $ from 'jquery';

Vue.prototype.$echarts = echarts
import moment from 'moment'//导入文件 
Vue.use(ant)
Vue.prototype.$moment = moment;//赋值使用
Vue.use(ElementUI, {
    size: 'small'
});
Vue.use(ElementUI);
Vue.use(Animate);
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>'
})


router.beforeEach((to, from, next) => {

    let authLogin = sessionStorage.getItem('login')

    if (from.fullPath == '/login') {

        if (to.matched.some(record => record.meta.requireAuth)) {//判断该路由是否需要登录权限（只有登录路由不需要权限）

            if (authLogin) {
                next()

            } else {
                next(false)
                alert('请登录')
            }
        }

    } else if (to.fullPath == '/login') {
        sessionStorage.clear()
        next(true)
    } else {

        next()

    }
})


