<template>
  <div class="warp">
    <div id="Flow">
      <div class="left">
        <h3 class="title">数据配置模块</h3>
        <p class="sele_warp">
          <label style="color:#fff">选择数据源:</label>
          <el-select v-model="dataSourceValue" placeholder="请选择">
            <el-option
              v-for="item in dataSource"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </p>
        <p class="sele_warp">
          <label style="color:#fff">选择存储路径:</label>
          <el-autocomplete
            class="inline-input"
            v-model="state1"
            :fetch-suggestions="querySearch"
            placeholder="请输入内容"
            @select="handleSelect"
          ></el-autocomplete>
        </p>
        <p class="sele_warp">
          <label style="color:#fff">储存文件大小:</label>
          <!-- <el-select v-model="storeValue" placeholder="请选择">
            <el-option
              v-for="item in Store"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>-->
          <el-input-number
           type="number"
            v-model="storeValue"
            controls-position="right"
            @change="handelChange"
            :min="1"
          ></el-input-number>
          <span style="color:#fff;padding-left:20px">MB</span>
        </p>
        <p class="sele_warp">
          <label style="color:#fff">间隔时间:</label>
          <!-- <el-select v-model="timeValue" placeholder="请选择">
            <el-option
              v-for="item in timeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>-->
          <el-input-number
           type="number"
            v-model="timeValue"
            controls-position="right"
            @change="handelChange"
            :min="1"
          ></el-input-number>
          <span style="color:#fff;padding-left:20px">min</span>
        </p>

        <p style="text-align:right;">
          <el-button size="small" @click="startBtn()" :loading="startLoading">开始</el-button>
          <el-button size="small" style="margin-right:34px" @click="stopBtn()" :loading="stopLoading">停止</el-button>
        </p>
      </div>
      <div class="right">
        <div class="right_header">
         <div>
            <span style="padding:0 20px 0 50px">
            <a-badge status="processing" text/>
          </span>
          实时日志
         </div>
         <div style="cursor: pointer;" @click="refresh()">
           <i class="el-icon-refresh"></i>
           刷新
         </div>
        </div>
        <div class="right_bottom">
          <!-- <Loading :loading="loading"/> -->
          <el-table
            :data="tableData"
            style="width:100%;opacity: 0.8;"
            size="small"
            height="300"
            v-loading="loading"
            element-loading-text="拼命加载中..."
            element-loading-spinner="el-icon-loading"
            element-loading-background="#DCDCDC"
          >
            <el-table-column type="index" label="序号" align="center" min-width="60"></el-table-column>
            <el-table-column prop="time" label="日期" align="center" min-width="60"></el-table-column>
            <el-table-column prop="countPa" label="数据包大小" align="center" min-width="60"></el-table-column>
            <el-table-column prop="countByte" label="数据总字节数" align="center" min-width="60"></el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  startOfflineServer,
  stopOfflineServer,
  offlineLog,
  getPath
} from "../../api/api.js";
import Loading from "./Loading";
export default {
  mounted() {
    // this.restaurants = this.loadAll();
    this.offlineLog(); //离线日志
    this.getPath();
    this.setInt();
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer);
    }
  },
  data() {
    return {
      startLoading:false,//开始加载
      stopLoading:false,//停止加载
      loading:false,//表格加载
      timer: null, //定时器
      state1: "", //选择存储路径
      restaurants: [],
      dataSource: [], //数据源下拉
      dataSourceValue: "", //数据源值
      Store: [
        {
          value: "储存",
          label: "325"
        }
      ], //储存下拉
      storeValue: "50", //储存值
      timeOptions: [
        {
          value: "时间",
          label: "325"
        }
      ], //时间下拉
      timeValue: "30", //时间值
      tableData: []
    };
  },
  methods: {
    setInt() {
      if (this.timer) {
        clearInterval(this.timer);
      } else {
        this.timer = setInterval(() => {
          this.offlineLog();
        }, 10000);
      }
    },
    //数据源请求
    getPath() {
      getPath().then(res => {
        if (res.code == 0) {
          this.restaurants = [{ value: res.data.descDir }];
          this.dataSource = [
            // { value: res.data.descDir, label: res.data.descDir }
            { value: res.data.srcDir, label: res.data.srcDir }
          ];
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    //刷新日志
    refresh(){
      this.offlineLog("succ")
    },
    //日志信息
    offlineLog(val) {
      this.loading = true
      offlineLog().then(res => {
        if (res.code == 0) {
          this.tableData = res.data;
          this.loading = false
          if(val){
             this.$message({
              message: '刷新成功',
              type: "success"
            });
          }
        } else {
          this.$message.error(res.msg);
            this.loading = true
        }
      });
    },
    //input方法
    querySearch(queryString, cb) {
      var restaurants = this.restaurants;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },

    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    // loadAll(){
    //   return[
    //     {
    //       "value":"dsfsdfsf","address":"21321"
    //     }
    //   ]
    // },
    //储存路径下拉
    handleSelect(item) {
      console.log(item);
    },
    //开始方法
    startBtn() {
      console.log(
        this.state1,
        this.dataSourceValue,
        this.timeValue,
        this.storeValue
      );
      if (
        this.state1 == "" ||
        this.storeValue == "" ||
        this.timeValue == "" ||
        this.dataSourceValue == ""
      ) {
        return this.$message({
          message: "请输入完整",
          type: "warning"
        });
      } else {
        this.startLoading = true
        startOfflineServer({
          fileMaxSize: this.storeValue,
          fileMaxMinute: this.timeValue,
          descDir: this.state1,
          srcDir: this.dataSourceValue
        }).then(res => {
          if (res.code == 0) {
             this.startLoading = false
            this.$message({
              message: res.msg,
              type: "success"
            });
          } else {
             this.startLoading = false
            this.$message.error(res.msg);
          }
        });
      }
    },
    //停止方法
    stopBtn() {
        this.stopLoading = true
      stopOfflineServer().then(res => {
        if (res.code == 0) {
           this.stopLoading = false
          this.$message({
            message: res.msg,
            type: "success"
          });
        } else {
           this.stopLoading = false
          this.$message.error(res.msg);
        }
      });
    },
    //计数器方法
    handelChange(value) {
      console.log(value);
    }
  },
  watch: {
    msgObj: function(a) {
      this.dataSource = [
        { value: this.msgObj.srcDir, label: this.msgObj.srcDir }
      ];
      this.restaurants = [{ value: this.msgObj.descDir }];
    }
  },
  components: {
    Loading
  }
};
</script>

<style >
#Flow {
  width: 80%;

  height: 500px;
  margin-top: 50px;
  padding: 10px 100px;
  display: flex;
}
.left {
  width: 416px;
  height: 350px;
  /* margin:0 50px; */
  background: url("../../assets/image/gg3.png") no-repeat;
  background-size: cover;
  border-bottom: 2px solid #205785;
}
.right {
  flex: 1;
  height: 350px;
  border: 1px solid #4ca0d4;
  margin-left: 50px;
}
.right_bottom {
  position: relative;
  width: 90%;
  margin: 0 auto;
}
.right_header {
  
  height: 40px;
  border-bottom: 1px solid #4ca0d4;
  color: #fff;
  line-height: 40px;
  font-weight: bold;
  display:flex;
  justify-content: space-between;
  padding:0 20px
}
.title {
  text-align: center;
  padding-top: 15px;
  color: #fff;
}
.sele_warp {
  text-align: center;
  padding-top: 20px;
}
/* 挣扎着 */
.el-input__inner {
  background-color: transparent;
  color: #fbfcfc;
}
.el-scrollbar__view {
  background: #062448;
}
.el-select-dropdown__item.hover,
.el-select-dropdown__item:hover {
  background-color: transparent;
}
.el-select-dropdown__item.selected {
  color: #fff;
}
label {
  display: inline-block;
  width: 125px;
  text-align: right;
  font-weight: bold;
}
.el-select-dropdown__item {
  color: #fff;
}
.el-button {
  background: #1f4570;
  color: #fff;
}
.el-table,
.el-table__expanded-cell {
  background-color: transparent;
}
.el-table--border::after,
.el-table--group::after,
.el-table::before {
  background-color: transparent;
}
.el-table th,
.el-table tr {
  background-color: transparent;
}
.el-table th,
.el-table tr {
  border-bottom: none;
  color: #ccc;
}
.el-table td,
.el-table th.is-leaf {
  border-bottom: none;
}
tr {
  pointer-events: none;
}
.warp {
  display: flex;
  align-items: center;
  justify-content: center;
}
.ant-badge-status-dot {
  width: 10px;
  height: 10px;
}
.el-autocomplete-suggestion__wrap {
  background-color: transparent;
}
::-webkit-scrollbar {
  width: 10px;
  height: 1px;
}

::-webkit-scrollbar-track {
  background-color: #fff;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  background-color: skyblue;
  border-radius: 10px;
  background-image: -webkit-linear-gradient(
    45deg,
    rgba(255, 255, 255, 0.5) 25%,
    transparent 25%,
    transparent 50%,
    rgba(255, 255, 255, 0.5) 50%,
    rgba(255, 255, 255, 0.5) 75%,
    transparent 75%,
    transparent
  );
}
</style>






