<template>
  <div class="warp">
    <div class="mode">
      <div>
        <div style="text-align:center">
          <el-button size="small" @click="btn('已连接')">已连接</el-button>
          <el-button size="small" @click="btn('配置连接')">配置连接</el-button>
          <el-button size="small" @click="btn('配置文件规则')">配置文件规则</el-button>
        </div>
        <!-- 已连接 -->
        <div>
          <div class="leftMode" v-if="activeBtn == '已连接'">
            <h3 class="title">已连接</h3>
            <div style="width:438px;height:301px;opacity: 0.8;">
              <div
                style="width:98%;height:249px;margin-left: 13px;overflow: auto"
                v-loading="Spanloading"
                element-loading-text="拼命加载中..."
                element-loading-spinner="el-icon-loading"
                element-loading-background="#DCDCDC"
              >
                <ul class="ul">
                  <li v-for="(x,index) in arr" :key="index">
                    <span>{{x.ip+" : "+x.port}}</span>
                    <el-button size="mini" @click="stopSocket(x)">{{x.socktState == true?"断开":"连接"}}</el-button>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
        <!-- 已连接结束 -->
        <!-- 配置连接 -->
        <div class="right_top_1" v-if="activeBtn == '配置连接'">
          <h3 class="title">配置连接</h3>
          <p class="sele_warp_mode">
            <label style="color:#fff;line-height:20px;">IP:</label>
            <el-input placeholder="请输入内容" v-model="IPInput" clearable></el-input>
          </p>
          <p class="sele_warp_mode">
            <label style="color:#fff;line-height:20px;">端口号:</label>
            <el-input placeholder="请输入内容" v-model="ProtInput" clearable></el-input>
          </p>
          <p class="radio">
            <el-radio v-model="radio" label="1">主站</el-radio>
            <el-radio v-model="radio" label="2">小站</el-radio>
          </p>
          <p class="sele_warp" style="text-align:left">
            <label style="color:#fff;line-height:20px;">阵地信息:</label>
            <el-select v-model="positionInput" placeholder="请选择" style="margin-left:20px">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </p>
          <p>
            <el-button
              size="small"
              style="margin:30px 107px 0 0;float:right"
              @click="btns()"
              :loading="samlLoading"
            >连接</el-button>
          </p>
        </div>
        <!-- 配置连接结束 -->

        <!-- 配置文件规则 -->
        <div class="right_top_2" v-if="activeBtn == '配置文件规则'">
          <h3 class="title">配置文件规则</h3>
          <p class="sele_warp">
            <label style="color:#fff">选择存储路径:</label>
            <el-autocomplete
              class="inline-input"
              v-model="state1"
              :fetch-suggestions="querySearch"
              placeholder="请输入内容"
              @select="handleSelect"
            ></el-autocomplete>
          </p>
          <p class="sele_warp">
            <label style="color:#fff">储存文件大小:</label>
            <!-- <el-select v-model="storeValue" placeholder="请选择">
              <el-option
                v-for="item in Store"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>-->
            <el-input-number
              type="number"
              v-model="storeValue"
              controls-position="right"
              @change="handelChange"
              :min="1"
            ></el-input-number>
            <span style="color:#fff;padding-left:20px">MB</span>
          </p>
          <p class="sele_warp">
            <label style="color:#fff">间隔时间:</label>
            <!-- <el-select v-model="timeValue" placeholder="请选择">
              <el-option
                v-for="item in timeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>-->
            <el-input-number
              type="number"
              v-model="timeValue"
              controls-position="right"
              @change="handelChange"
              :min="1"
            ></el-input-number>
            <span style="color:#fff;padding-left:20px">min</span>
          </p>

          <p style="text-align:right;padding-top:30px">
            <el-button size="small" @click="startBtn()" :loading="startLoading">开始</el-button>
            <el-button
              size="small"
              style="margin-right:34px"
              @click="stopBtn()"
              :loading="stopLoading"
            >停止</el-button>
          </p>
        </div>
        <!-- 配置文件规则结束 -->
      </div>

      <div class="right" style="margin-top:30px">
        <div class="right_header">
          <div>
            <span style="padding:0 20px 0 50px">
            <a-badge status="processing" text/>
          </span>
          实时日志
         </div>
         <div style="cursor: pointer;" @click="refresh()">
           <i class="el-icon-refresh"></i>
           刷新
         </div>
        </div>
        <div class="right_bottom">
          <el-table
            :data="tableData"
            style="width:100%;opacity: 0.8;"
            size="small"
            height="300"
            v-loading="loading"
            element-loading-text="拼命加载中..."
            element-loading-spinner="el-icon-loading"
            element-loading-background="#DCDCDC"
          >
            <el-table-column type="index" label="序号" align="center" min-width="60"></el-table-column>
            <el-table-column prop="time" label="日期" align="center" min-width="60"></el-table-column>
            <el-table-column prop="countPa" label="数据包大小" align="center" min-width="60"></el-table-column>
            <el-table-column prop="countByte" label="数据总字节数" align="center" min-width="60"></el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  connect,
  getClients,
  disconnect,
  startSocketServer,
  stopSocketServe,
  onlineLog,
  getPath
} from "../../api/api.js";
export default {
  mounted() {
    this.getClientsList(); //已连接方法
    this.onlineLog(); //日志信息
    this.getPath();
    this.setInt();
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer);
    }
  },
  data() {
    return {
      startLoading: false, //开始加载
      stopLoading: false, //停止加载
      samlLoading: false, //连接加载
      Spanloading: false, //已连接加载
      loading: false, //表格加载
      timer: null, //定时器
      state1: "", //选择存储路径
      restaurants: [], //存储路径数组，已存在的数
      activeBtn: "已连接",
      radio: "1", //主站或小站
      IPInput: "", //IP
      ProtInput: "", //端口
      positionInput: "", //阵地信息
      Store: [
        {
          value: "储存",
          label: "325"
        }
      ], //储存下拉
      storeValue: "50", //储存值
      timeOptions: [
        {
          value: "时间",
          label: "325"
        }
      ], //时间下拉
      timeValue: "30", //时间值
      options: [
        {
          value: "1",
          label: "阵地一"
        },
        {
          value: "2",
          label: "阵地二"
        }
      ], //阵地信息
      arr: [], //已连接数据
      value: "",
      tableData: []
    };
  },
  methods: {
    setInt() {
      if (this.timer) {
        clearInterval(this.timer);
      } else {
        this.timer = setInterval(() => {
          this.onlineLog();
        }, 10000);
      }
    },
    //数据源请求
    getPath() {
      getPath().then(res => {
        if (res.code == 0) {
          this.restaurants = [{ value: res.data.filePath }];
        } else {
          this.$messsage.error(res.msg);
        }
      });
    },
    //日志刷新
    refresh(){
      this.onlineLog("succ")
    },
    //日志信息
    onlineLog(val) {
      this.loading = true;
      onlineLog().then(res => {
        if (res.code == 0) {
          this.tableData = res.data;
          this.loading = false;
          if(val){
             this.$message({
              message: '刷新成功',
              type: "success"
            });
          }
          
        } else {
          this.$message.error(res.msg);
          this.loading = true;
        }
      });
    },
    //已连接List
    getClientsList() {
      this.Spanloading = true;
      getClients().then(res => {
        if (res.code == 0) {
          this.arr = res.data;
          this.Spanloading = false;
        } else {
          this.Spanloading = true;
          this.$message.error(res.msg);
        }
      });
    },
    //断开连接
    stopSocket(val) {
      if (val.socktState == true) {
        disconnect({
          ip: val.ip,
          port: val.port
        }).then(res => {
          if (res.code == 0) {
            this.$message({
              message: res.msg,
              type: "warning"
            });
            this.getClientsList();
          } else {
            this.$message.error(res.msg);
          }
        });
      } else {
        connect({
          ip: val.ip,
          port: val.port
        }).then(res => {
          if (res.code == 0) {
            this.getClientsList();
            this.$message({
              message: res.msg,
              type: "success"
            });
          } else {
            this.$message.error(res.msg);
          }
        });
      }
    },
    //连接
    btns() {
      // console.log(this.IPInput, this.ProtInput, this.positionInput, this.radio);
      // var str = "ip=192.168.16.108"+"&"+ "port=8888"
      if (
        this.IPInput == "" ||
        this.ProtInput == "" ||
        this.positionInput == "" ||
        this.radio == ""
      ) {
        return this.$message({
          message: "请输入完整",
          type: "warning"
        });
      } else {
        this.samlLoading = true;
        connect({
          ip: this.IPInput,
          port: this.ProtInput,
          position: this.positionInput,
          ismain: this.radio == "1" ? true : false
        }).then(res => {
          if (res.code == 0) {
            this.samlLoading = false;
            this.getClientsList();
            this.$message({
              message: res.msg,
              type: "success"
            });
          } else {
            this.samlLoading = false;
            this.$message.error(res.msg);
          }
        });
      }
    },
    //判断左盒子
    btn(val) {
      this.activeBtn = val;
    },
    //input方法
    querySearch(queryString, cb) {
      var restaurants = this.restaurants;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },

    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    // loadAll(){
    //   return[
    //     {
    //       "value":"dsfsdfsf","address":"21321"
    //     }
    //   ]
    // },
    handleSelect(item) {
      // console.log(this.IPInput,this.ProtInput,this.positionInput,this.radio);
    },
    //开始
    startBtn() {
      console.log(this.state1, this.storeValue, this.timeValue);
      if (this.state1 == "" || this.storeValue == "" || this.timeValue == "") {
        return this.$message({
          message: "请输入完整",
          type: "warning"
        });
      } else {
        this.startLoading = true;
        startSocketServer({
          fileMaxSize: this.storeValue,
          fileMaxMinute: this.timeValue,
          filePath: this.state1
        }).then(res => {
          if (res.code == 0) {
            this.startLoading = false;
            this.$message({
              message: res.msg,
              type: "success"
            });
          } else {
            this.startLoading = false;
            this.$message.error(res.msg);
          }
        });
      }
    },
    //停止方法
    stopBtn() {
      this.stopLoading = true;
      stopSocketServe().then(res => {
        if (res.code == 0) {
          this.stopLoading = false;
          this.$message({
            message: res.msg,
            type: "success"
          });
        } else {
          this.stopLoading = false;
          this.$message.error(res.msg);
        }
      });
    },
    //计数器方法
    handelChange(value) {
      console.log(value);
    }
  }
};
</script>

<style lang="less">
.mode {
  /* margin-top: 50px;
  padding: 10px 100px;
  display: flex; */
  width: 80%;
  height: 500px;
  margin-top: 50px;
  padding: 10px 100px;
  display: flex;
}
.leftMode {
  width: 450px;
  height: 350px;
  /* margin:0 50px; */

  background: url("../../assets/image/gg3.png") no-repeat;
  background-size: cover;
  text-align: center;
  border-bottom: 2px solid #205785;
  /* overflow:auto; */
}
.ul {
  list-style: none;
  /* margin-top: 50px; */
}
.ul li {
  color: #fff;
  line-height: 50px;
}
.ul li span {
  padding: 0 20px;
  font-weight: bold;
}
.rightMode {
  flex: 1;
}
.right_top {
  display: flex;
}

.right_top_1 {
  width: 450px;
  height: 350px;
  border-bottom: 2px solid #205785;
  /* margin:0 50px; */
  background: url("../../assets/image/fff.png") no-repeat;
  /* background-size: cover; */
  /* text-align: center; */
}

.right_top_2 {
  border-bottom: 2px solid #205785;
  width: 450px;
  height: 350px;
  /* margin:0 50px; */
  background: url("../../assets/image/fff.png") no-repeat;
  /* background-size: cover; */
  text-align: center;
}
.sele_warp_mode {
  display: flex;
  text-align: center;
  padding: 10px 0;
}

.title {
  text-align: center;
  padding-top: 15px;
  color: #fff;
}
.right_bottom {
  width: 90%;
  margin: 0 auto;
}
.right_bottom_1 {
  border: 1px solid #4ca0d4;
}
.sele_warp_mode {
   .el-input__inner {
    width: 80%;
  }
}
.radio {
  display: flex;
  align-items: center;
  justify-content: center;
   .el-radio {
    width: 50px;
  }
}
.sele_warp_mode {
  .el-input {
    width: 54%;
  }
}

::-webkit-scrollbar {
  width: 10px;
  height: 1px;
}

::-webkit-scrollbar-track {
  background-color: #a366ff;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  background-color: skyblue;
  border-radius: 10px;
  background-image: -webkit-linear-gradient(
    45deg,
    rgba(255, 255, 255, 0.2) 25%,
    transparent 25%,
    transparent 50%,
    rgba(255, 255, 255, 0.2) 50%,
    rgba(255, 255, 255, 0.2) 75%,
    transparent 75%,
    transparent
  );
}
</style>





